import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useHttpClient } from "../../shared/hooks/http-hook"; //api  
import { useDispatch } from "react-redux";
import { loginUser } from "../../redux/actions/userActions";
import { useParams } from "react-router-dom";
import { KAKAO_AUTH_URL } from "./Oauth";
import { NAVER_AUTH_URL } from "./Oauthnaver";
import { jwtDecode as jwt_decode } from "jwt-decode";

import "./login.css";

const code = new URL(window.location.href).searchParams.get("code");

export const refreshAccessToken = async () => {
  try {
    const refreshToken = localStorage.getItem("refreshToken");
    if (!refreshToken) {
      console.error("  .");
      return;
    }

    const url = new URL(
      `${process.env.REACT_APP_BACKEND_URL}/accounts/refresh-token`
    );
    url.searchParams.append("refresh_token_key", refreshToken);

    const responseData = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ refresh_token_key: refreshToken }),
    });

    if (responseData.ok) {
      const data = await responseData.json();
      const newAccessToken = data.access_token;

      localStorage.setItem("accessToken", newAccessToken);
      // console.log("  !", newAccessToken);
    } else {
      // console.error("   .");
      // console.error(" :", responseData.status);
    }
  } catch (error) {
    // console.error("     .", error);
  }
};

const Login = () => {
  const { isLoading, sendRequest, clearError } = useHttpClient(); // useHttpClient  

  const [id, setID] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { code } = useParams();

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      // Enter   handleLogin  
      handleLogin();
    }
  };
  const handleLogin = async () => {
    if (!id || !password) {
      setError("   .");
      return;
    }

    try {
      const formData = new FormData();
      formData.append("username", id);
      formData.append("password", password);
      const responseData = await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/accounts/login`,
        "POST",
        formData
      );

      const { access_token, refresh_token, platform_type, admin } =
        responseData;

      // const expires_in = 9;
      // const expirationTime = Date.now() * expires_in;

      const expiresIn = 3600;
      const expirationTime = new Date().getTime() + expiresIn * 1000;

      dispatch(
        loginUser(access_token, refresh_token, platform_type, admin === 1)
      );

      dispatch({
        type: "LOGIN_USER",
        payload: {
          platformType: platform_type,
          isAdmin: admin === true,
        },
      });

      localStorage.setItem("isLoggedIn", "true");
      localStorage.setItem("accessToken", access_token);
      localStorage.setItem("refreshToken", refresh_token);
      localStorage.setItem("accessTokenExpiration", expirationTime);

      console.log(responseData.admin);

      setError(null);
      navigate("/");
    } catch (error) {
      setError(" .  .");
    }
  };

  // //     
  // function isAccessTokenExpired(accessToken) {
  //   // JWT     
  //   const decodedToken = jwt_decode(accessToken);
  //   //     
  //   const expirationTime = decodedToken.exp;
  //   //      ,   
  //   const currentTime = Math.floor(Date.now() / 1000);
  //   //         
  //   return expirationTime < currentTime;
  // }

  // async function performTokenRefresh() {
  //   if (isAccessTokenExpired(localStorage.getItem("accessToken"))) {
  //     //       
  //     await refreshAccessToken();
  //   }
  // }

  // setInterval(performTokenRefresh, 1 * 60 * 1000);

  useEffect(() => {
    const accessToken = localStorage.getItem("accessToken");
    if (accessToken) {
      navigate("/");
    }
  }, [navigate]);

  return (
    <div className="login-page">
      <img className="welcome" src="/img/loginimg/Welcome.png" alt="Welcome" />
      <p className="text_welcome">!</p>
      <form className="login-form">
        <input
          type="text"
          value={id}
          onChange={(e) => setID(e.target.value)}
          className="login-input"
          onKeyPress={handleKeyPress}
        />
        <br />
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className={error ? "error-input" : "login-input"}
          onKeyPress={handleKeyPress}
        />
        {error && <p className="error-message">{error}</p>}
        <button
          type="button"
          onKeyPress={handleKeyPress}
          className="login-button"
          onClick={handleLogin}
        >
          
        </button>
        <div className="nosignup">
          <p>  ?</p>
          <p>
            <Link to="/Signup" className="signup">
              
            </Link>
          </p>
        </div>
      </form>
      <div className="icon_content">
        <div className="icon_wrapper">
          <a href={KAKAO_AUTH_URL} className="icon_text">
            <img className="icons" src="/img/loginimg/Kakao.png" alt="kakao" />
            <p> </p>
          </a>
        </div>

        <div className="icon_wrapper">
          <a href={NAVER_AUTH_URL} className="icon_text">
            <img className="icons" src="/img/loginimg/Naver.png" alt="naver" />
            <p> </p>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Login;
