import React from "react";
import { NavLink } from "react-router-dom";

import "./MenuLinks.css";

const MenuLinks = () => {
  return (
    <React.Fragment>
      <li className="header_links_menu_list">
        <NavLink to="/main/goods"></NavLink>
      </li>
      <li className="header_links_menu_list">
        <NavLink to="/PurchaseForm"></NavLink>
      </li>
      <li className="header_links_menu_list">
        <NavLink to="/main/rating"> </NavLink>
      </li>
      <li className="header_links_menu_list">
        <NavLink to="/main/ratingsystem"></NavLink>
      </li>
    </React.Fragment>
  );
};

export default MenuLinks;
